.sendToFeedback{
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 300px;
    background-color: #FFFFFF;
}

.title{
    font-size: 70px;
    letter-spacing: -0.1em;
}

.contents{
    margin-top: 15px;
    font-size: 25px;
    letter-spacing: -0.1em;
    color: #666666;
}

.button{
    margin-top: 30px;
    width: 144px;
    height: 30px;
    border-radius: 20px;
    border: none;
    background-color: #FFDAB9;
    font-size: 14px;
    letter-spacing: -0.1em;
}